.app {
  min-width: 1200px;
  max-width: 1600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.link {
  text-decoration: none;
  transition: opacity 0.5s linear;
}

.link:hover {
  opacity: 0.6;
  cursor: pointer;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 30px 120px;
}

.header__links {
  display: flex;
  grid-gap: 40px;
  gap: 40px;
  align-items: center;
}

.header__link {
  text-decoration: none;
  font-family: 'Jost';
  font-style: normal;
  font-weight: 400;
  font-size: 19px;
  line-height: 30px;
  letter-spacing: 0.4px;
  color: #292f36;
}

.header__link:first-child {
    color: #CDA274;
}

.interior {
  position: relative;
}

.interior__background {
  width: 100%;
}

.interior__container {
  z-index: 1;
  display: flex;
  flex-direction: column;
  position: absolute;
  max-width: 595px;
  left: 120px;
  top: 188px;
}

.interior__header {
  font-family: 'DM Serif Display';
  font-style: normal;
  font-weight: 400;
  font-size: 80px;
  line-height: 96px;
  letter-spacing: 0.22px;
  color: #292f36;
  margin: 0;
}

.interior__text {
  font-family: 'Jost';
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 33px;
  letter-spacing: 0.22px;
  color: #4d5053;
  margin: 19px 0 35px;
}

.interior__button {
  background-color: #292f36;
  border: 2px solid #292f36;
  border-radius: 18px;
  height: 71px;
  width: 207px;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 0 20px 0 34px;
  justify-content: space-between;
}

.interior__button_text {
  font-family: 'Jost';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.72px;
  color: #ffffff;
}

.interior__button_arrow {
  height: 25px;
}

